<template>
  <div class="login flex">
    <div class="main">
      <div class="main-title">
        <span>音视频通信</span>
      </div>
      <van-form>
        <van-field
          v-model.trim="username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: false, message: '请填写用户名' }]"
        />
        <van-field
          v-model.trim="channelid"
          name="房间号"
          label="房间号"
          placeholder="房间号"
          :rules="[{ required: true, message: '请填写房间号' }]"
        />
        <div style="margin: 16px">
          <van-button
            type="info"
            block
            :disabled="channelid === '' || !isSupportRtc"
            @click="handleClickJoinBtn"
            >进入房间</van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
// 引入计算token的方法
import sha256 from '../assets/js/sha256'
// 引入vant方法
import { Toast } from 'vant'
// 引入vuex的map方法
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'Login',
  data() {
    return {
      isSupportRtc: false
    }
  },
  computed: {
    ...mapState(['userName', 'channelId']),
    username: {
      get() {
        return this.userName
      },
      set(value) {
        this.UpdateUserName(value)
      }
    },
    channelid: {
      get() {
        return this.channelId
      },
      set(value) {
        this.UpdatechannelId(value)
      }
    }
  },
  methods: {
    ...mapMutations(['UpdateUserName', 'UpdatechannelId', "UpdateVideoDevice", "UpdateAudioDevice"]),
    /**
     * 加入房间
     * 触发: 输入用户名,房间号(必填),单击加入房间按钮
     * 提示加入房间中
     * 获取鉴权信息
     * 加入房间
     * 页面跳转
     */
    async handleClickJoinBtn() {
      // 1. 提示加入房间中
      Toast.loading({
        message: '加入房间...',
        forbidClick: true // 禁用背景点击
      })
      // 2. 获取授权信息
      const authInfo = await this.getAuthInfo()
      // 3. 加入房间
      this.$aliWebrtc.joinChannel(authInfo, this.userName).then(
        () => {
          // 加入房间成功
          Toast.success('加入房间!')
          // 4. 页面跳转
          this.$router.push({
            name: 'metting'
          })
        },
        (error) => {
          // 加入房间失败
          Toast.fail('加入房间失败: ' + error.message)
        }
      )
    },
    /**
     * 校验是否支持音视频
     */
    checkSupportRtc() {
      this.$aliWebrtc
        .isSupport()
        .then((res) => {
          //支持webrtc
          this.isSupportRtc = true
          this.UpdateVideoDevice(res.videoDevice)
          this.UpdateAudioDevice(res.audioDevice)
        })
        .catch((error) => {
          //不支持webrtc
          Toast.fail('support err: ' + error.message)
          this.isSupportRtc = false
        })
    },
    /**
     * 获取鉴权信息
     * 配置appId,key
     */
    async getAuthInfo() {
      const appId = 'hil6ydzc' // 修改为自己的appid 该方案仅为开发测试使用，正式上线需要使用服务端的AppServer
      const key = '8172773aeb6531f8fb9efd5162e6a08c' // 修改为自己的key 该方案仅为开发测试使用，正式上线需要使用服务端的AppServer
      const userId = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
        /[xy]/g,
        function (c) {
          var r = (Math.random() * 16) | 0,
            v = c == 'x' ? r : (r & 0x3) | 0x8
          return v.toString(16)
        }
      ) // 可以自定义
      const timestamp = parseInt(new Date().getTime() / 1000 + 48 * 60 * 60)
      const nonce = 'AK-' + timestamp
      const token = await this.getToken({
        appId,
        key,
        channelId: this.channelId,
        userId,
        nonce,
        timestamp
      })
      return {
        appid: appId,
        userid: userId,
        timestamp: timestamp,
        nonce: nonce,
        token: token,
        gslb: ['https://rgslb.rtc.aliyuncs.com'],
        channel: this.channelId
      }
    },
    getToken({ appId, key, channelId, userId, nonce, timestamp }) {
      return new Promise((resolve) => {
        setTimeout(() => {
          let token = sha256(
            appId + key + channelId + userId + nonce + timestamp
          )
          resolve(token)
        }, 1000)
      })
    }
  },
  created() {
    // 检测是否支持webrtc
    this.checkSupportRtc()
  }
}
</script>

<style lang="less" scoped>
@import url('../assets/css/variable.less');
.login {
  height: 100vh;
  padding: 0 20px;
  background-color: @color_theme;
  &.flex {
    align-items: center;
    justify-content: center;
  }
  .main {
    width: 420px;
    max-width: 100%;
    background-color: #fff;
    padding: 30px 26px 24px 26px;
    font-size: 14px;
    .main-title {
      font-size: 30px;
      text-align: center;
      letter-spacing: 1px;
    }
  }
}
</style>