<template>
  <div class="metting flex">
    <!-- 左边栏 -->
    <div class="sideBar">
      <h2 class="logo">
        <a href="/"></a>
      </h2>
    </div>
    <!-- 主视图 -->
    <div class="local-container">
      <div class="local-message">
        <p>
          用户名: <strong>{{ userName }}</strong>
        </p>
        <p>
          房间号: <strong>{{ channelId }}</strong>
        </p>
      </div>
      <div class="local-video">
        <!-- 本地摄像头视图 -->
        <video v-show="isOpenCamera" autoplay ref="localVideo"></video>
        <van-empty
          v-show="!isOpenCamera"
          class="custom-image"
          image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
          description="摄像头未开启"
        />
        <div class="btn-group">
          <!-- 禁用摄像头 设备有摄像头时才出现 -->
          <van-icon
            v-if="videoDevice"
            name="photograph"
            :color="isOpenCamera ? '#fff' : '#666'"
            size="30px"
            @click="handleClickCameraBtn"
          />
          <br />
          <!-- 关闭麦克风 设备有麦克风时才出现 -->
          <van-icon
            v-if="audioDevice"
            name="volume"
            :color="isOpenAudio ? '#fff' : '#666'"
            size="30px"
            @click="handleClickAudioBtn"
          />
        </div>
        <!-- 退出视频 -->
        <div class="btn-leave">
          <van-icon
            name="phone-circle"
            color="red"
            size="50px"
            @click="handleClickLeaveBtn"
          />
        </div>
      </div>
    </div>
    <!-- 成员列表 -->
    <div class="remote-container">
      <h2>房间成员</h2>
      <ul>
        <li v-for="remote in remotes" :key="remote.userId">
          <video
            autoplay
            :muted="remote.muteAudioPlaying"
            :ref="'remote' + remote.userId"
          ></video>
          <div class="remote-name">{{ remote.displayName }}</div>
          <button
            type="button"
            :class="remote.muteAudioPlaying ? '' : 'active'"
            @click="
              handleClickRemoteAudioBtn(remote.userId, !remote.muteAudioPlaying)
            "
          >
            静音
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// 引入vant方法
import { Toast } from 'vant'
// 引入vuex的方法
import { mapState } from "vuex"

export default {
  name: 'Metting',
  data() {
    return {
      isOpenCamera: false, // 是否打开摄像头
      isOpenAudio: false, // 是否打开麦克风
      remotes: [] // 房间成员
    }
  },
  computed: {
    ... mapState(["userName", "channelId", "audioDevice", "videoDevice"])
  }, 
  methods: {
    /**
     * 打开/关闭本地摄像头
     * 触发: 单击摄像头按钮
     * 提示打开/关闭中
     * 推/不推视频流
     */
    handleClickCameraBtn() {
      const { isOpenCamera, isOpenAudio } = this
      // 提示关闭/打开摄像头中
      Toast.loading({
        message: isOpenCamera ? '关闭摄像头...' : '打开摄像头...',
        forbidClick: true // 禁用背景点击
      })
      // 配置推流设置 重新推流
      this.publishVideo(
        !isOpenCamera,
        isOpenAudio,
        isOpenCamera ? '摄像头已关闭' : '摄像头已打开'
      )
    },
    /**
     * 打开/关闭本地麦克风
     * 触发: 单击麦克风按钮
     * 提示打开/关闭中
     * 推/不推音频流
     */
    handleClickAudioBtn() {
      const { isOpenCamera, isOpenAudio } = this
      // 提示关闭/打开麦克风中
      Toast.loading({
        message: isOpenAudio ? '关闭麦克风...' : '打开麦克风...',
        forbidClick: true // 禁用背景点击
      })
      // 配置推流设置 重新推流
      this.publishVideo(
        isOpenCamera,
        !isOpenAudio,
        isOpenAudio ? '麦克风已关闭' : '麦克风已打开'
      )
    },
    /**
     * 退出房间
     * 触发: 点击挂断按钮
     */
    // 监听点击退出房间按钮
    handleClickLeaveBtn() {
      // 退出房间
      this.leaveChannel()
    },
    /**
     * 静音某用户
     * 触发: 点击某用户的静音按钮
     * 静音/收听某用户
     */
    handleClickRemoteAudioBtn(userId, enable) {
      // 开启/关闭某个远程用户的静音
      this.$aliWebrtc.muteRemoteAudioPlaying(userId, enable)
      // 更新该远程用户信息
      this.remotes.map((remote, index) => {
        if (remote.userId === userId) {
          // 重新获取该用户信息
          this.remotes.splice(index, 1, this.$aliWebrtc.getUserInfo(userId))
        }
      })
    },
    /**
     * 初始化本地推流
     */
    initLocalVideo() {
      // 有摄像头或者麦克风设备就进行推流
      const { audioDevice, videoDevice } = this
      if (audioDevice || videoDevice) {
        this.publishVideo(videoDevice, audioDevice)
      }
    },
    /**
     * 初始化订阅远程用户
     * 获取远程用户列表
     * 默认订阅远程用户的音视频
     */
    initRemotes() {
      // 获取远程用户
      const remotes = this.$aliWebrtc.getUserList()
      this.remotes = remotes
      // 订阅远程用户
      remotes.map((remote) => {
        this.subscribeRemote(remote.userId)
      })
    },
    /**
     * 远程用户加入房间 onJoin
     * 更新远程用户列表
     * 提示
     */
    listenRemoteJoin() {
      this.$aliWebrtc.on('onJoin', (publisher) => {
        // 更新用户列表
        this.remotes.push(publisher)
        // 提示
        Toast.success(`${publisher.displayName}进入房间`)
      })
    },
    /**
     * 远程用户推流 onPublisher
     * 订阅该远程用户
     * 更新远程用户列表
     */
    listenRemotePublish() {
      this.$aliWebrtc.on('onPublisher', (publisher) => {
        // 订阅该用户
        this.subscribeRemote(publisher.userId)
      })
    },
    /**
     * 远程用户离开 onLeave
     * 更新远程用户列表
     * 取消订阅该用户
     * 提示
     */
    listenRemoteLeave() {
      this.$aliWebrtc.on('onLeave', (publisher) => {
        // 更新用户列表
        this.remotes.map((remote, index) => {
          if (remote.userId === publisher.userId) {
            this.remotes.splice(index, 1)
          }
        })
        // 取消订阅
        this.$aliWebrtc.unSubscribe(publisher.userId).then(() => {
        },(error)=>{
          Toast.fail("unSubscribe err: " + error.message)
        });
        // 提示
        Toast.success(`${publisher.displayName}离开房间`)
      })
    },
    // 本地-推流
    publishVideo(isOpenCamera, isOpenAudio, successToast = '') {
      // 配置推流音视频
      this.$aliWebrtc.configLocalAudioPublish = isOpenAudio
      this.$aliWebrtc.configLocalCameraPublish = isOpenCamera
      // 进行推流
      this.$aliWebrtc.publish().then(
        () => {
          // 推流成功
          if (successToast) {
            Toast.success(successToast)
          }
          // 按钮状态
          this.isOpenCamera = isOpenCamera
          this.isOpenAudio = isOpenAudio
          // 更新本地视频预览
          this.showLocalVideo()
        },
        (error) => {
          // 推流失败
          Toast.fail('publish err: ' + error.message)
        }
      )
    },
    // 本地-预览
    showLocalVideo() {
      this.$aliWebrtc
        .startPreview(this.$refs.localVideo)
        .then(() => {})
        .catch((error) => {
          // 预览失败
          Toast.fail('startPreview err: ' + error.message)
        })
    },
    // 本地-离开房间
    leaveChannel() {
      this.$aliWebrtc.leaveChannel().then(
        () => {
          Toast.success('退出房间')
          // 返回登录页
          this.$router.replace({ path: '/' })
          location.reload()
        },
        (error) => {
          Toast.fail('leaveChannel err: ' + error.message)
        }
      )
    },
    // 远程-订阅
    subscribeRemote(userId) {
      // 配置远程用户音视频
      this.$aliWebrtc.configRemoteCameraTrack(userId, true, true)
      this.$aliWebrtc.configRemoteAudio(userId, true)
      // 订阅远程用户
      this.$aliWebrtc.subscribe(userId).then(
        (userId) => {
          // 展现远程用户音视频
          this.$aliWebrtc.setDisplayRemoteVideo(
            userId, // userId是用户ID
            this.$refs['remote' + userId][0], // html中video元素
            1 // 1表示摄像头流（大流和小流）和音频流，2表示屏幕分享流
          )
          // 更新远程用户信息
          this.remotes.map((remote, index) => {
            if (remote.userId === userId) {
              this.remotes.splice(index, 1, this.$aliWebrtc.getUserInfo(userId))
            }
          })
        },
        (error) => {
          // 订阅失败
          Toast.fail('subscribe err: ' + error.message)
        }
      )
    }
  },
  mounted() {
    // 1. 默认本地推流
    this.initLocalVideo()
    // 2. 获取远程用户 订阅远程用户的音视频
    this.initRemotes()
    // 3. 监听远程用户加入
    this.listenRemoteJoin()
    // 4. 监听远程用户推流
    this.listenRemotePublish()
    // 5. 监听远程用户离开
    this.listenRemoteLeave()
  },
  beforeDestroy() {
    // 退出房间
    this.leaveChannel()
  }
}
</script>

<style lang="less" scoped>
@import url('../assets/css/variable.less');
@media (max-width: 400px) {
  .metting {
    display: block;
    .sideBar {
      display: none;
    }
    .local-container {
      height: calc(100vh - 120px);
    }
    .remote-container {
      width: 100% !important;
      height: 120px;
      h2 {
        display: none;
      }
      ul {
        display: flex;
        overflow-x: scroll;
        height: 100% !important;
        align-items: center;
        li {
          margin-right: 20px;
          margin-bottom: 0 !important;
          flex-shrink: 0;
          width: 100px !important;
          height: 90px !important;
        }
      }
    }
  }
}
.metting {
  height: 100vh;
  .sideBar {
    width: 100px;
    background: @color_metting;
    color: #fff;
    .logo {
      height: 50px;
      padding: 10px;
      a {
        display: block;
        height: 100%;
        background: url('../assets/logo.png') no-repeat center;
        background-size: contain;
      }
    }
  }
  .local-container {
    flex: 1;
    position: relative;
    .local-message {
      height: 50px;
      background: @color_metting;
      color: #fff;
      p {
        display: inline-block;
        margin: 0 30px;
        line-height: 50px;
        font-size: 15px;
      }
      button {
        &.active {
          background-color: @color_theme;
        }
      }
    }
    .local-video {
      width: 100%;
      height: calc(100% - 50px);
      background-color: #000;
      position: relative;

      video {
        height: 100%;
        width: 100%;
        display: block;
        background-color: #000;
        object-fit: cover;
      }
      .van-empty {
        height: 100%;
      }
      .btn-group {
        position: absolute;
        right: 20px;
        top: 20px;
        i {
          margin-bottom: 10px;
        }
      }
      .btn-leave {
        position: absolute;
        left: 50%;
        bottom: 40px;
        transform: translateX(-50%);
        width: 44px;
        height: 44px;
        background-color: #fff;
        border-radius: 50%;
        i {
          left: -3px;
          top: -3px;
        }
      }
    }
  }
  .remote-container {
    width: 300px;
    background-color: @color_metting;
    color: #fff;
    h2 {
      height: 50px;
      font-size: 20px;
    }
    ul {
      height: calc(100% - 50px);
      overflow-y: scroll;
      padding: 0 15px;
      &::-webkit-scrollbar {
        display: none;
      }
      li {
        margin-bottom: 20px;
        width: 100%;
        height: 180px;
        position: relative;
        video {
          background: #000;
          width: 100%;
          height: 100%;
          border-radius: 10px;
          object-fit: cover;
        }
        .remote-name {
          position: absolute;
          left: 50%;
          bottom: 15px;
          padding: 4px 15px;
          background: rgba(255, 255, 255, 0.5);
          color: #fff;
          font-size: 14px;
          border-radius: 4px;
          transform: translateX(-50%);
        }
        button {
          position: absolute;
          right: 10px;
          bottom: 15px;
          &.active {
            background-color: @color_theme;
          }
        }
      }
    }
  }
  .remote-user-list {
    width: 200px;
    background: #333;
    color: #fff;
    flex-shrink: 0;
    h2 {
      font-size: 20px;
      line-height: 36px;
      font-weight: 600;
      text-align: center;
    }
    .user-ul {
      .user-ul-li {
        font-size: 20px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        position: relative;
        &:hover {
          background: #666;
        }
        .menu {
          position: absolute;
          z-index: 100;
          left: 200px;
          text-align: left;
          top: 1px;
          background-color: #333333;
          border-radius: 8px;
          li {
            padding: 0 10px;
            min-width: 165px;
            height: 40px;
            line-height: 40px;
            color: #ddd;
            text-indent: 10px;
            cursor: pointer;
            span {
              cursor: pointer;
              &:hover {
                color: #38f;
              }
            }
          }
        }
      }
    }
  }
  .publisher {
    margin: 20px 0 20px calc(50 / 1080 * 100vh);
    input[type='text'] {
      border: 2px solid #456879;
      border-radius: 10px;
      height: 40px;
      width: 95px;
    }
    button {
      font-family: arial;
      color: #ffffff !important;
      font-size: 12px;
      width: 68px;
      height: 26px;
      background: deepskyblue;
      border: none;
      outline: none;
      &:hover {
        background: #006eff;
        cursor: pointer;
      }
    }
    .select-preview {
    }
    .push-stream {
    }
    .streamType {
      labeL {
        font-family: arial;
        font-size: 16px;
      }
    }
  }
}
</style>