import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

const actions = {}
// 操作数据
const mutations = {
    UpdateUserName(state, param){
        state.userName = param
    },
    UpdatechannelId(state, param){
        state.channelId = param
    },
    UpdateVideoDevice(state, param){
        state.videoDevice = param
    },
    UpdateAudioDevice(state, param){
        state.audioDevice = param
    }
}
// 存储数据
const state = {
    userName: Math.random()
    .toString(36)
    .replace(/[^a-z]+/g, '')
    .substr(0, 5),// 用户名 默认随机用户名
    channelId: "",// 房间号
    audioDevice: false,// 本地是否有麦克风设备
    videoDevice: false// 本地是否有摄像头设备
}

export default new Vuex.Store({
    actions,
    mutations,
    state
})
