import Vue from 'vue'
import App from './App.vue'
// 引入路由器
import VueRouter from "vue-router"
// 引入路由器规则
import router from "./router"
// 引入vuex仓库
import store from "./store"
// 引入sdk
import 'aliyun-webrtc-sdk'
// 引入vant
import "./plugins/vant"


Vue.config.productionTip = false

Vue.use(VueRouter)

new Vue({
  router,
  store,
  render: h => h(App),
  beforeCreate() {
    /**
   * 创始音视频实例,丢在全局$aliWebrtc上  自动推流, 自动订阅
   */
    Vue.prototype.$aliWebrtc = new window.AliRtcEngine({autoPublish: true, autoSubscribe: true});
  }
}).$mount('#app')
